.footer
{
    display: flex;
    flex-direction: column;
    height: 500px;
    background: #7002ff;
}
.footer-top
{
    display:flex;
    height: 400px;
    
}
.footer-left
{
    width:50%;
    
    
}
.footer-left h1
{
    font-size: 60px;
    margin: 0;
    padding: 0;
    color:white;
}
.footer-left p
{
    color:white;
    font-size: 20px;
}
.footer-left h1,
.footer-left p
{
    margin-left: 150px;
    margin-top: 25px;
    word-spacing: 5px;
}
.footer-right
{
    width: 60%;
}
.footer-bottom
{
    height: 100px;
    text-align: center;
    font-size: 20px;
    background-color:#7203F4;
    
}
.footer-bottom p
{
    margin-top: 40px;
    
}
.footer-right
{
    display:flex;
    flex-direction: column;
}
.footer-div-icon
{
    display: flex;
    flex-direction: inline;
    margin: 25px;
}
.footer-div-icon span
{
    margin-left: 15px;
    margin-top: 18px;
    font-size: 30px;
    color:white;
}
.footer-icon
{
    color:white;
}
@media (max-width: 981px)
{
    
    .footer-left h1,
    .footer-left p
    {
        margin-left: 20px;
    }
    .footer-div-icon span
    {
        font-size: 20px;
    }
    
}