.projects
{
   
    display: flex;
    flex-direction: row;
    justify-content: center;
    background:  #7002ff;
    padding:2rem;
    
}
.project
{
    text-align:center;
    background: white;
    border-radius: 20px;
    margin: 45px; 
    width: 100%;
    height: 800px;
    display: grid;
    grid-template-rows: 1fr 1fr 1fr 1fr;
   
    
    
}
.project h1
{
    font-size: 40px;
    color: #0a0a0a;
    padding-top: 40px;
}
.project p
{
    font-size: 30px;
    color: #0a0a0a;
    padding-left: 20px;
    padding-right: 20px;
    
}
.project img
{
    width: 40%;
    border: solid 10px #7002ff;
    border-radius:50%;
    align-self: center;
    justify-self: center;

}

.project a
{
    color: white;
    text-decoration: none;
    font-size: 25px;
    font-weight: bold;
    letter-spacing: 2px;
    background: #7002ff;
    height: 65px;
    width: 170px;
    border: white;
    border-radius: 100px;
    align-self: center;
    justify-self: center;
    padding-top: 15px;

}
.project a:hover,
.project a:active
{
    background: white;
    color: #7002ff;
    border: solid 3px #7002ff;
}
@media (max-width: 980px)
{
   .projects
   {
       flex-direction: column;
       align-items: center;
       
   }
   .project
   {
    width: 80%
   }

    
}
