.header
{
    position: sticky;
    top: 0;
    left: 0;
    width: 100%;
    background: #7002ff;
    height: 100px;
    box-shadow: 0px 0px 4px 0px grey ;
    z-index: 9999;
    
}
.header_navi
{
    display: flex;
    height: 100%;
    align-items: center;
    padding: 0 1rem;
    font-size: 25px;
}
.header_logo
{
    margin-left: 2rem;
    
    
    
}
.header_logo a
{
    margin-left: 2rem;
    color:white;

}
.header_logo a:hover
{
    color: #0a0a0a;
    cursor: pointer;
}
.header_item ul
{
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
}
.header_item li
{
    padding: 0 0.5rem;
    
}
.header_item li 
{
    text-decoration: none;
    color:White;
    font-weight: 700;
}

.header_item li:hover
{
    color: #0a0a0a;
    cursor: pointer;
}
.spacer
{
    flex: 1;
}

@media (max-width: 980px)
{
    .header_item
    {
        display: none;
    }
    .header_logo a
    {
        margin-left: 0
    }

    
}
@media (min-width: 981px)
{
    .header-toggle
    {
        display:none;
    }
    .header_logo
    {
        margin-left: 0;
    }
   
}